.radarChartPerformance{
  background-color: #282D30;
  border-radius: 5px;
  grid-column: 2;
  grid-row: 2;
  width: 100%;
  height: 100%;
}

.recharts-polar-grid-angle{
  line{
      stroke: none;
  }
}

.recharts-polar-angle-axis {
  transform: scaleY(1.1);
  transform-origin: 50% 30%;
}


.recharts-polar-angle-axis-tick-value {
  padding: 20px;
  tspan{
      font-size: 12px
  }
}